$blue:#3D5AFE;

header{
    
    z-index:200;
    min-height: 120vh;
    background-color: rgb(61, 90, 254);
    background-image: url("./img/-mountain.webp");
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;
    
    .headercontainer{
        height: 90vh;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .getintouch{
        display: flex;
        justify-content: flex-end;
        .gotocontact {
            button{
            background-color: transparent;
            border-width: 2px;
            color: white;
            }
            button:focus, button:hover{
                background-color:white;
                border: 2px solid white;
                ;
            }
        }
    }
    .headercontent{
        display: flex;
        flex-direction: column;
        .dan {
            text-transform: uppercase;
            h1{
                font-weight: bold;
                color: white;
                .blue{
                    color: $blue;
                }
            }
            h2 {
                color: white;
            }
            .links{
                margin: 2.5rem auto;
                a {
                    padding: 20px;
                }
            }
        } 
    }
    .scrollDown{
        display: flex;
        justify-content: center;
    }
    .scrollTop{
        display: none;
        img{
            border-radius: 50%;
            background-color:$blue;
            padding: 10px;
        }
        
        z-index:2000;
        position: fixed;
        bottom: 60px;
        right: 30px;
    }
    @media (min-width: 768px) {
        .scrollTop {
            display: block;
        }
    }

}
.section {
    padding: 1rem .5rem;

}
@media (min-width: 768px){
    .section{
        padding: 3rem 1.5rem;
    }
    .scrollTop{
        display: block;
    }
}