@font-face {
  font-family: 'Rokkit';
  font-display: auto;
  src: local('Rokkit'), url('./assets/fonts/Rokkitt/rokkitt.woff2') format('woff2');
}
body * {
  font-family: "Rokkitt", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body * {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
